.CreateLesson {
  width: 90%;
  margin: auto;
  border: 1px solid black;
  -webkit-box-shadow: 1px 2px 3px #ccc;
          box-shadow: 1px 2px 3px #ccc;
  border-radius: 4px;
  padding: 16px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  background: white;
  color: black;
}

.BackButtonCreate {
    display: block;
    float: left;
    -ms-flex-pack: center;
        justify-content: center;
    font-weight: lighter;
    width: 90px; 
    height: 40px; 
    border: none;
    background-color: white;
    font-size: 18px;
    color: black;
}

.ExampleSentence {
  display: inline-block;
  width: 90%;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto; 
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 16px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  background: white;
  color: black;
}

.ExampleSentenceHeader {
  display: block;
  margin: 6px 0 6px 5%;
  text-align: left;
  float: left;
  width: 80%;
  font-size: 22px;
}
.ExampleKey {
  display: block;
  margin: auto;
  clear: both;
  width: 100%;
}
.ExampleSentence ul {
  display: block;
  width: 60%;
  float: left;
  clear: right;
  text-align: left;
  list-style: none;
}
.ExampleSentence li{
  display: block;
  margin-right: 1.5rem;
}

.ExampleSentence li:first-child{
  list-style: none;
  font-weight: bold;
  font-size: 26px;
  border-bottom: 1px solid #eee;

}
.ExampleSentence li:last-child{
  color: #ccc;/*#EDE711;*/
}

.ExampleSentence li:nth-child(3){
  color: #00c4c3;
}

.ExampleSentenceWrapper {
  display: inline-block;
  border: 1px solid #eee;
  border-radius: 4px;
  margin-top: 10px;
  width: 90%;
  padding: 6px 0 6px 0;
}

.ExampleAnswerWrapper {
}

.ExampleHint {
  font-family: Roboto Mono, monospace;
  width: 5ch;
  color: #ccc;/*#EDE711;*/
  white-space: nowrap;
  text-align: center;
  display: inline;
  clear: both;
  overflow: hidden;
  margin-bottom: -3px;
  border-bottom: .05em solid gray;
  border-right: .05em solid;
  -webkit-animation: caret 1s steps(1) infinite;
          animation: caret 1s steps(1) infinite;
}
.FirstHalfExample,
.SecondHalfExample {
  position: absolute;
  display: inline;
  /*margin: 2em .5em 1em .5em;*/
}

.FirstHalfExample {
  left: 37%;
}

.SecondHalfExample{
  left: 52%;
}
.TypedTextExample {
  font-family: Roboto Mono, monospace;
  width: 6ch;
  white-space: nowrap;
  text-align: center;
  display: inline-block;
  clear: both;
  overflow: hidden;
  margin-bottom: -3px;
  border-bottom: .05em solid gray;
  border-right: .05em solid;
  -webkit-animation: typingEx 6s steps(6) infinite,
             caret 1s steps(1) infinite;
          animation: typingEx 6s steps(6) infinite,
             caret 1s steps(1) infinite;
}

.InputSentenceWrapper {
  display: inline-block;
  width: 90%;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto; 
  border: 1px solid grey;
  border-radius: 2px;
  padding: 16px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  background: white;
  color: black;
}
.CreateLesson span{
  display: block;
  width: 100%;
  float: left;
  color: red;
}

.InputSentenceWrapper p{
  float: left;
  margin: 0 0 0 5px;
  color: black;
}

.DeleteSentence {
  float: right;
  margin: 5px 5px 0 0;
  z-index: 99;
}



.DeleteSentence:hover,
.RemoveAlt:hover,
.ElementAdd:hover {
  cursor: pointer;
  fill: #00c4c3;
}

.LessonTitleInput{
  font-family: Open Sans, sans-serif;
  display: block;
  float: left;
  clear: both;
  width:60%;
  margin-top: 10px;
  margin-left: 4%;
  margin-bottom: 20px;
  border-top:none;
  border-left:none;
  border-right:none;
  background: none;
  
  height:40px;
  line-height:20px;
  font-size:40px;
  border-bottom:solid 2px lightgrey;
  -webkit-transition:all .23s ease-in;
  -o-transition:all .23s ease-in;
  transition:all .23s ease-in
}

.LessonTitleInput:focus {
  outline: none;
  border-bottom:solid 2px #EDE711;
}

.CreateSentenceButton {
  display: inline;
  margin-top: 12px;
}

.CreateButton {
  width: 120px;
  height: 60px;
  margin: 0 0 18px 80%;
  font-size: 18px;
  display: inline-block;
  clear: left;
  background: #00c4c3;
  color: white;
  border: none;
}

.CreateButton:hover {
  color: black;
  border: initial;
}

.CreateButton:disabled {
  background: #00c4c3;
  opacity: .5;
  color: black;
}

.ElementAddWrapper {
  display: inline-block;
  clear: both;
  float: left;
  margin: 0 auto 0 .6em;
  width: 100%;
}

.ElementAddButtonWrapper {
  background-color: none;
  color: none;
  border-style: none;
  display: block;
  clear: both;
  margin-left: 30%;
  outline: none;
}

.ElemenAdd{
  -o-transition: 100ms;
  transition: 100ms;
  -o-transition-timing-function: ease-in-out;
     transition-timing-function: ease-in-out;
  -webkit-transition: 100ms;
  -webkit-transition-timing-function: ease-in-out;
}

.Show {
  position: absolute;
  display: inline-block;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 99;
  background: #00c4c3;
  color: white;
  width: 5.9em;
  margin: 12% 0 0 -13%;
}


.Hide {
  display: none;

}

@-webkit-keyframes typingEx {
  from { width: 0 }
  0% {
    color: #00c4c3;
  }

  100% {
    color: #00c4c3;
  }
}

@keyframes typingEx {
  from { width: 0 }
  0% {
    color: #00c4c3;
  }

  100% {
    color: #00c4c3;
  }
}

@-webkit-keyframes caret {
  50% {
    border-right-color: transparent; 
  }
}

@keyframes caret {
  50% {
    border-right-color: transparent; 
  }
}

@media screen and (max-width: 1380px){
  .FirstHalfExample {
    left: 35%;
  }

  .SecondHalfExample{
    left: 52%;
  }
}

@media screen and (max-width: 690px) {

   .BackButtonCreate{
    padding: 0;
    margin-bottom: 18px;
    font-size: 14px;
    width: 55px;
    height: 18px;
  }

  .ExampleSentenceWrapper {
    width: 95%;
    padding: 12px 0 12px 0;
  }
  .FirstHalfExample{
    display: block;
    position: relative;
    left: 0;

  }
  .SecondHalfExample {
    display: block;
    position: relative;
    left: 0;
    /*margin: 8px auto 8px auto;*/
  }

  .ExampleSentence ul {
    margin: 10px auto 0 auto;
    padding-left: 0;
    float: left;
    text-align: left;
    list-style: none;
    width: 100%;
  }

  .CreateButton {
    margin-left: 65%;
  }

  .LessonTitleInput {
    width: 80%;
    margin: 0 0 6px 0;
    font-size: 30px;
    height:30px;
    line-height:30px;
  }
  .InputAnswer,
  .InputHint,
  .InputSentence {
    width: 100%;
    margin: 0 0 2px 0;
    font-size: 20px;
    height:25px;
    line-height:20px;
  }

  .AltAddWrapper {
    display: inline-block;
    margin: 0 0 0 0;
  }

  .AddAltButtonWrapper {
    margin-left: 25%;
  }

  .InputAnswer,
  .InputHint {
    width: 50%;
  }

  .DeleteSentence {
    margin: -3px -8px 0 0;
  }
  .InputSentenceWrapper p{
    float: left;
    margin: -9px 0 0 -8px;
  }
}


