.player {

	display: inline-block;
	width: 23%;
	margin-left: 25px;
}

.player svg {
	display: block;
	stroke: white;
	-webkit-transform: rotate(-90deg);
	    -ms-transform: rotate(-90deg);
	        transform: rotate(-90deg);
	-webkit-animation: all 1s ease-in forward;
	        animation: all 1s ease-in forward;

}

.player circle{
	display: block;
}

.name {
	position:absolute;
	margin-top: -125px;
	margin-left: 37px;
	font-family: Open Sans, sans-serif;
	font-size: 35px;
	text-align: center;
	color: white;
}

.score {
	position:absolute;
	margin-top: -85px;
	margin-left: 80px;
	font-family: Open Sans, sans-serif;
	font-size: 18px;
	text-align: center;
	color: #655;
}

@media screen and (max-width: 640px) {

	.player {
		display: block;
	}

}



