.login{
    font-family: Montserrat, sans-serif;
    width:100%;
    height:100%;
    display:block;
    -ms-flex-pack:center;
        justify-content:center;
    text-align: center;
    margin: 100px 0 300px 0;
    

  }

.login-form{
    width: 90%;
    height: 50vh;
    margin: auto;
    -ms-flex-pack:center;
        justify-content:center;
    -ms-flex-direction:column;
        flex-direction:column;
    margin-top: 10px;
    background: white;
    border: 1px solid #eee;
    -webkit-box-shadow: 1px 2px 1px #eee;
            box-shadow: 1px 2px 1px #eee;
  }



.login-form input{
  font-family: Helvetica, sans-serif;
  -ms-flex-pack: center;
      justify-content: center;
  max-width: 150px;
  margin-top: 10px;
  border-top:none;
  border-left:none;
  border-right:none;
  background: none;
  text-align: center;
  height:40px;
  line-height:20px;
  font-size:40px;
  border-bottom:solid 2px #046A91;
}
.login-form input:focus{
    border-bottom:solid 2px #EDE711;
    outline:none
}
.login-form .error{
    text-align:center;
    margin:5px 0;
    padding:5px 10px;
    color:#c92c43
  }

.title {
    display: inline-block;
    /*justify-content: space-around;*/
    display: -webkit-flex;
    /*-webkit-justify-content: space-around;*/
    margin: 50px 0 0 35px;
    
}


.text {
  color: #23D5AB;
  font-size: 65px;
  font-weight: 650;
  font-family: Montserrat;
  font-weight: bold;
  position: relative;
  -webkit-animation: jump 2.5s ease-out infinite ;
  animation: jump 2.5s ease-out infinite; 
  
}

.text-w {
    -webkit-animation-delay:250ms;
            animation-delay:250ms;
  }.text-a {
    -webkit-animation-delay:450ms;
            animation-delay:450ms;
  }.text-i {
    -webkit-animation-delay:650ms;
            animation-delay:650ms;
  }.text-t {
    -webkit-animation-delay:850ms;
            animation-delay:850ms;
  }.text-i2 {
    -webkit-animation-delay:1050ms;
            animation-delay:1050ms;
  }.text-n {
    -webkit-animation-delay:1250ms;
            animation-delay:1250ms;
  }.text-g {
    -webkit-animation-delay:1450ms;
            animation-delay:1450ms;
  }.text-1 {
    -webkit-animation-delay:1650ms;
            animation-delay:1650ms;
  }.text-2 {
    -webkit-animation-delay:1850ms;
            animation-delay:1850ms;
  }.text-3 {
    -webkit-animation-delay:2050ms;
            animation-delay:2050ms;
  }

@keyframes jump {
    0% {
            color: #23D5AB;
            top: 0px;
        }
    25% {
          top: 0px;
    }
    50% {  
            
            top: 0px;
        }
    75% {
          top: 0px;
    }

    90% {
          color: #23A6D5;
          top: -30px;
    }
    100% {  
            color: #23D5AB;
            top: 0px;
        }
}

@-webkit-keyframes jump {
     0% {
            color: #23D5AB;
            top: 0px;
        }
    50% {  
            color: #23A6D5;
            top: -30px;
        }

    100% {  
            color: #23D5AB;
            top: 0px;
        }
}




