.BackButton {
	font-size: 22px;
	border: none;
	background: none;
	color: gray;
	float: left;
}

.BackButton:hover {
	
	color: black;
	-webkit-box-shadow: none;
	        box-shadow: none;

}


.Waiting {
	/*display: inline-block;*/
	width: 90%;
	margin:20px 10px 12px 50px; 
	border: 1px solid grey;
	-webkit-box-shadow: 1px 2px 3px #ccc;
	        box-shadow: 1px 2px 3px #ccc;
	border-radius: 4px;
	padding: 16px;
	text-align: center;
	font-family: 'Roboto', sans-serif;
	background: white;
	color: black;
}

.Waiting h1{
	font-family: 'Open Sans';
	text-align: center;
	clear: both;
}

.Waiting h2{
	font-weight: lighter;
	clear: both;
}

.Waiting h3{
	font-family: 'Roboto', sans-serif;
	font-weight: lighter;
	font-size: 40px;

}

.Waiting button {
  display: block;
  margin: 8px auto 8px auto;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: lighter;
  width: 110px; 
  height: 40px; 
  border: 1px solid #eee;
  border-radius: 2px;
  background-color: white;
  font-size: 18px;
  color: black;

} 

.Waiting button:hover {
  cursor: pointer;
  color: #00c4c3;
  border: 1px solid #ccc;
  -webkit-box-shadow: 0px 1px 2px 0px #eee;
          box-shadow: 0px 1px 2px 0px #eee;
}

.Waiting  button:disabled {
  color: black;
  background-color: #d58282;
  opacity: .7;
  
}
.Waiting button:focus {
	text-decoration: none;
}
.Waiting button:disabled:hover {
  cursor: auto;
}


.Team {

	display: inline-block;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 25%;
	margin:20px; 
	border: 1px solid grey;
	-webkit-box-shadow: 1px 2px 3px #ccc;
	        box-shadow: 1px 2px 3px #ccc;
	border-radius: 4px;
	padding: 16px;
	text-align: center;
	font-family: 'Helvetica', sans-serif;
	background: white;
	color: black;
}


@media screen and (max-width: 640px) {

.WaitingWrapper {
	width: 100%

}

.Waiting {
	display: inline-block;
	width: 90%;
	margin:0 0 0 5px; 
	border: 1px solid grey;
	-webkit-box-shadow: 1px 2px 3px #ccc;
	        box-shadow: 1px 2px 3px #ccc;
	border-radius: 4px;
	padding: 16px;
	text-align: center;
	font-family: 'Helvetica', sans-serif;
	background: white;
	color: black;
}

.Waiting h1{
	text-align: center;
	clear: both;
}

.Waiting h2{
	font-weight: lighter;
	clear: both;
}

.Waiting h3{
	font-family: 'Helvetica', sans-serif;
	font-weight: lighter;
	font-size: 40px;

}

.Waiting button{
	width: 30%;
	-webkit-font-kerning: normal;
	        font-kerning: normal;
}

.BackButton {
	margin-left: -12px;
}

}


